body {
    background: hsl(233, 47%, 7%);
    margin: 0;
}

p {
    color: hsla(0, 0%, 100%, 0.75);
}

.insights {
    color: hsl(277, 64%, 61%);
}

.container {
    background: hsl(244, 38%, 16%);
    text-align: center;
    border-radius: 10px;
    width: 70%;
    margin: 5em auto;
}



.image {
    border-radius: 10px 10px 0 0;

}

.sub-container {
    padding: 1.2em;
}

.sub-h1 {
    color: white;
    margin-top: 0ch;
}

.stat h1 {
    margin: 0%;
    margin-top: 0.5em;
    color: aliceblue;
}

.stat p {
    margin: 0;
}

@media (min-width:520px) {
    .container {
        display: flex;
        width: 75%;
        margin: 0 auto;
        margin: 10em auto;
    }

    .sub-container {
        order: 1;
        padding: 1.5em;
    }
    .image-container{
        order: 2;
    }

    .stat-container {
        display: flex;
        justify-content: space-evenly;
        align-content: center;
    }
}